:host {
  display: block;
  box-sizing: border-box;
  border-radius: var(--mdx-comp-accordion-border-radius-default);
  overflow: hidden;
  border-top-width: var(--mdx-comp-accordion-border-width-top);
  border-bottom-width: var(--mdx-comp-accordion-border-width-bottom);
  border-right-width: var(--mdx-comp-accordion-border-width-right);
  border-left-width: var(--mdx-comp-accordion-border-width-left);
  cursor: pointer;

  .accordion {
    width: 100%;
    border: none;
    padding: 0;
    cursor: pointer;

    &:focus-visible {
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-style: solid;
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-offset: -4px;
    }

    &__summary {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: var(--mdx-comp-accordion-padding-horizontal-default) var(--mdx-comp-accordion-padding-vertical-default);
      font: var(--mdx-comp-accordion-closed-font-default);
      font-weight: var(--mdx-comp-accordion-closed-font-weight-title);
      word-break: break-all;
      transition: var(--mdx-comp-accordion-transition-hover-fade-in-out);
    }

    &__rating {
      display: flex;
      align-items: center;
      gap: var(--mdx-comp-star-rating-container-spacing-gap);
      max-height: 0;
    }

    &__paragraph {
      margin: 0;
      padding: 0 var(--mdx-comp-accordion-padding-horizontal-default) var(--mdx-comp-accordion-padding-vertical-default)
        var(--mdx-comp-accordion-padding-horizontal-default);
      word-break: break-all;
    }

    &__container {
      max-height: 0;
      overflow: hidden;
      transition: var(--mdx-comp-accordion-transition-expand-collapse);
    }

    &__icon {
      align-self: flex-start;
      transition: var(--mdx-comp-accordion-transition-icon-chevron);
    }
  }
}

:host([variant='white']) {
  background-color: var(--mdx-comp-accordion-closed-background-color-on-white);
  border-color: var(--mdx-comp-accordion-closed-border-color-on-white);
  font: var(--mdx-comp-accordion-closed-font-default);

  .accordion {
    &__container {
      background-color: var(--mdx-comp-accordion-closed-background-color-on-white);
      border-color: var(--mdx-comp-accordion-closed-border-color-on-white);
      font: var(--mdx-comp-accordion-closed-font-default);
    }

    &__icon {
      height: var(--mdx-comp-accordion-closed-sizing-icon);
      width: var(--mdx-comp-accordion-closed-sizing-icon);
      color: var(--mdx-comp-accordion-closed-icon-color-default);
    }

    &__summary {
      background-color: var(--mdx-comp-accordion-closed-background-color-on-white);
      color: var(--mdx-comp-accordion-closed-title-color-on-white);

      &:hover {
        color: var(--mdx-comp-accordion-closed-title-color-hover);
        background-color: var(--mdx-comp-accordion-closed-background-color-hover);
        border-color: var(--mdx-comp-accordion-closed-border-color-hover);
      }
    }
  }
}

:host([variant='white'][open]) {
  color: var(--mdx-comp-accordion-open-text-color-on-white);
  font: var(--mdx-comp-accordion-open-font-default);

  .accordion {
    &__summary {
      color: var(--mdx-comp-accordion-open-title-color-on-white);

      &:hover {
        color: var(--mdx-comp-accordion-open-title-color-hover);
        background-color: var(--mdx-comp-accordion-open-background-color-hover);
      }
    }

    &__icon {
      height: var(--mdx-comp-accordion-open-sizing-icon);
      width: var(--mdx-comp-accordion-open-sizing-icon);
      color: var(--mdx-comp-accordion-open-icon-color-default);
      transform: rotate(180deg);
    }
  }
}

:host([variant='grey']) {
  background-color: var(--mdx-comp-accordion-closed-background-color-on-grey);
  border-color: var(--mdx-comp-accordion-closed-border-color-on-grey);
  font: var(--mdx-comp-accordion-closed-font-default);

  .accordion {
    &__container {
      border-color: var(--mdx-comp-accordion-closed-border-color-on-grey);
      background-color: var(--mdx-comp-accordion-open-background-color-on-grey);
      font: var(--mdx-comp-accordion-open-font-default);
    }

    &__summary {
      background-color: var(--mdx-comp-accordion-closed-background-color-on-grey);
      color: var(--mdx-comp-accordion-closed-title-color-on-grey);

      &:hover {
        color: var(--mdx-comp-accordion-closed-title-color-hover);
        background-color: var(--mdx-comp-accordion-closed-background-color-hover);
        border-color: var(--mdx-comp-accordion-closed-border-color-hover);
      }
    }

    &__icon {
      height: var(--mdx-comp-accordion-closed-sizing-icon);
      width: var(--mdx-comp-accordion-closed-sizing-icon);
      color: var(--mdx-comp-accordion-closed-icon-color-default);
    }
  }
}

:host([variant='grey'][open]) {
  background-color: var(--mdx-comp-accordion-open-background-color-on-grey);
  font: var(--mdx-comp-accordion-open-font-default);

  .accordion {
    &__summary {
      color: var(--mdx-comp-accordion-open-title-color-on-grey);

      &:hover {
        color: var(--mdx-comp-accordion-open-title-color-hover);
        background-color: var(--mdx-comp-accordion-open-background-color-hover);
      }
    }

    &__icon {
      height: var(--mdx-comp-accordion-open-sizing-icon);
      width: var(--mdx-comp-accordion-open-sizing-icon);
      color: var(--mdx-comp-accordion-open-icon-color-default);
      transform: rotate(180deg);
    }
  }
}

details > summary::-webkit-details-marker {
  display: none;
}
