import { Component, Element, Event, EventEmitter, Host, Listen, Prop, h } from '@stencil/core';
import { ACCORDION_VARIANTS } from './export';

@Component({
  tag: 'mdx-accordion',
  styleUrl: 'accordion.scss',
  shadow: true,
})
export class Accordion {
  @Element()
  host: HTMLMdxAccordionElement;

  @Event()
  accordionClick: EventEmitter<{ open: boolean; item?: string }>;

  @Prop({ reflect: true })
  open = false;

  @Prop({ reflect: true })
  variant: (typeof ACCORDION_VARIANTS)[keyof typeof ACCORDION_VARIANTS] = ACCORDION_VARIANTS.WHITE;

  @Prop()
  summary: string;

  @Prop({ reflect: true })
  item?: string;

  @Listen('click', { target: 'window' })
  handleOutsideClick(event: MouseEvent) {
    if (this.open && !this.host.contains(event.target as Node)) {
      this.open = false;
      this.toggleAccordion();
    }
  }

  componentDidLoad() {
    const { previousElementSibling, nextElementSibling } = this.host;

    if (previousElementSibling) {
      this.host.style.borderTopLeftRadius = '0';
      this.host.style.borderTopRightRadius = '0';
    }
    if (nextElementSibling) {
      this.host.style.borderBottomLeftRadius = '0';
      this.host.style.borderBottomRightRadius = '0';
    }
  }

  private toggleAccordion = () => {
    const container = this.host.shadowRoot?.querySelector('.accordion__container') as HTMLDivElement | null;
    if (!container) return;
    if (this.open) {
      container.style.maxHeight = container?.scrollHeight + 'px';
    } else {
      container.style.maxHeight = '0';
    }
  };

  render() {
    return (
      <Host>
        <button
          type="button"
          aria-expanded={this.open}
          class="accordion"
          onClick={() => {
            this.open = !this.open;
            this.accordionClick.emit({ open: this.open, item: this.item });
            this.toggleAccordion();
          }}
        >
          <div class="accordion__summary">
            {this.host.querySelector('mdx-star-rating') ? (
              <div class="accordion__rating">
                {this.summary}
                <slot name="star-rating" />
              </div>
            ) : (
              this.summary
            )}
            <mdx-icon-chevron-down class="accordion__icon" />
          </div>
        </button>
        <div class="accordion__container">
          <p class="accordion__paragraph">
            <slot name="details" />
          </p>
        </div>
      </Host>
    );
  }
}
